html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.Alert-bg-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  background: rgba(0, 0, 0, 0.71); }

.Alert-container {
  width: 360px;
  height: 161px;
  border-radius: 10px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  border: solid 1px #d3d3d3;
  background-color: #ffffff;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }
  .Alert-container .Alert-title {
    width: 100%;
    height: 45px;
    margin-top: 16px;
    display: inline-block;
    font-weight: 500;
    font-family: Roboto;
    font-size: 14px;
    line-height: 1.14;
    text-align: center;
    color: #000000; }
  .Alert-container .Alert-message {
    width: 100%;
    height: 56px;
    font-family: Roboto;
    font-size: 14px;
    line-height: 1.43;
    text-align: center;
    color: #000000; }
  .Alert-container .Alert-control {
    width: 100%;
    height: 42px;
    border: solid 1px #e2e2e2;
    background-color: #f0f0f0;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px; }
  .Alert-container .buttonflex {
    display: flex;
    margin-top: 10px;
    margin-bottom: 10px;
    flex-flow: row-reverse;
    justify-content: center; }
    .Alert-container .buttonflex .primarysecondary {
      border-radius: 20px;
      text-align: center;
      font-family: Roboto;
      font-size: 12px;
      display: flex;
      justify-content: center;
      display: flex;
      margin-left: 9px;
      background: transparent; }
      .Alert-container .buttonflex .primarysecondary:hover {
        color: #fff; }
  .Alert-container .primary {
    width: 83px;
    height: 22px;
    border-radius: 20px;
    border-radius: 20px;
    border: solid 1px #e61f1d;
    background: linear-gradient(to bottom, #e6221a, #ff125b);
    color: #fff;
    line-height: 100%; }
  .Alert-container .secondary {
    width: 83px;
    height: 22px;
    border-radius: 20px;
    border: solid 1px #4b50c3;
    background-color: #e9e9f3; }
    .Alert-container .secondary .secchar {
      color: #4b50c3;
      width: 50px;
      height: 14px;
      font-family: Roboto;
      font-size: 12px; }

@-webkit-keyframes zoom-out-enter {
  0% {
    opacity: 0;
    transform: scale(1.3); }
  100% {
    opacity: 1;
    transform: scale(1); } }

@-moz-keyframes zoom-out-enter {
  0% {
    opacity: 0;
    transform: scale(1.3); }
  100% {
    opacity: 1;
    transform: scale(1); } }

@keyframes zoom-out-enter {
  0% {
    opacity: 0;
    transform: scale(1.3); }
  100% {
    opacity: 1;
    transform: scale(1); } }

@-webkit-keyframes zoom-out-leave {
  0% {
    opacity: 1;
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1.3); } }

@-moz-keyframes zoom-out-leave {
  0% {
    opacity: 1;
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1.3); } }

@keyframes zoom-out-leave {
  0% {
    opacity: 1;
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1.3); } }
