html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.sidenav {
  height: 100%;
  width: 17%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-image: linear-gradient(to bottom, #e6221a, #ff125b);
  border-radius: 0px 17px 17px 0px;
  overflow-x: hidden; }
  .sidenav .logo-wrapper {
    border-bottom: 1px solid #e61f1d; }
    .sidenav .logo-wrapper .logo {
      width: 120px;
      height: 65px;
      align-items: center;
      margin: auto;
      margin-top: 20px;
      margin-bottom: 20px; }
      .sidenav .logo-wrapper .logo img {
        width: 100%;
        height: 100%; }
  .sidenav .profile {
    position: absolute;
    width: 100%;
    height: 130px;
    bottom: 0;
    color: #fff;
    cursor: pointer; }
    .sidenav .profile::before {
      opacity: 0.1;
      background: #000;
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1; }
    .sidenav .profile .user-logout,
    .sidenav .profile .user-details {
      height: 60px;
      padding: 10px 32px;
      display: flex;
      align-items: center;
      border-top: 1px solid rgba(0, 0, 0, 0.1); }
      .sidenav .profile .user-logout .menu-list,
      .sidenav .profile .user-details .menu-list {
        padding-left: 25px; }
      .sidenav .profile .user-logout .logout-icon,
      .sidenav .profile .user-details .logout-icon {
        width: 20px;
        height: 20px; }
    .sidenav .profile .user-logout .menu-icon {
      padding-left: 10px;
      padding-top: 5px; }
    .sidenav .profile .user-details {
      height: 70px;
      cursor: default; }
      .sidenav .profile .user-details .menu-list {
        padding-left: 12px; }
      .sidenav .profile .user-details .menu-icon {
        width: 40px;
        height: 40px; }
        .sidenav .profile .user-details .menu-icon .user-logo {
          width: inherit;
          height: inherit; }

.sidenav a {
  padding: 15px 0px 15px 18.5px;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  display: flex;
  outline: none; }

.sidenav a:hover {
  color: #f1f1f1;
  text-decoration: none; }

.sidenavul {
  list-style-type: none;
  margin-bottom: 0px;
  padding-left: 24px;
  margin-top: 25px; }
  .sidenavul [data-key='Hub'] a .active-circle {
    background: #ed1d2e; }

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px; }
  .sidenav a {
    font-size: 18px; } }

.main {
  margin-left: auto;
  font-size: 14px;
  margin-left: 17%;
  padding: 30px 30px 25px 29px; }

.scrollbar {
  overflow-y: scroll;
  height: 100%; }

.nav-active {
  color: #e61f1d !important;
  background-color: #f3f4fb;
  border-radius: 32px 0px 0px 32px;
  position: relative; }
  .nav-active .active-area-top,
  .nav-active .active-area-bottom {
    width: 24px;
    height: 24px;
    background-color: #f3f4fb;
    position: absolute;
    right: 0px;
    overflow: hidden;
    z-index: -1; }
    .nav-active .active-area-top .active-circle,
    .nav-active .active-area-bottom .active-circle {
      position: absolute;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: #f3183e; }
  .nav-active .active-area-top {
    top: -24px; }
    .nav-active .active-area-top .active-circle {
      top: -24px;
      left: -24px; }
  .nav-active .active-area-bottom {
    bottom: -24px; }
    .nav-active .active-area-bottom .active-circle {
      bottom: -24px;
      left: -24px; }

.menu-list {
  display: inline-block;
  padding-left: 15px;
  align-self: center; }

.menu-icon {
  display: inline-block;
  width: 26px; }
